import React, { useState } from "react"
import { ArrowPathIcon, LockClosedIcon } from '@heroicons/react/24/solid'

import BaseTwo from "../Components/Base/BaseTwo"
import BackButton from "../Components/BackButton/BackButton"
import { changePassword, getUser } from "../Helpers"

const Profile = () => {
    
    const [data, setData] = useState({ oldPassword: '', newPassword: ''})
    const [change, setChange] = useState(false)
    const [loading, setLoading] = useState(false)

    const submitForm = async (e) => {
        setLoading(true)
        e.preventDefault()
        try {
            let change = await changePassword(data)
            if(change.success){
                setLoading(false)
                alert("Password Changed!")
            }
        }catch(err) {
            setLoading(false)
            alert("Something went wrong!")
        }
    }
    return(
        <BaseTwo header={<BackButton title="Profile" />} style="bg-white rounded-t-2xl">
            <div className="flex flex-col mt-10 items-center justify-center">
                <div className="flex flex-row items-center justify-center rounded-full w-[100px] h-[100px] bg-[#9376F4]">
                    <h1 className="text-4xl font-bold text-white uppercase">{getUser().firstName.charAt(0)}</h1>
                    <h1 className="text-4xl font-bold text-white uppercase">{getUser().lastName ? getUser().lastName.charAt(0): getUser().firstName.charAt(1)}</h1>
                </div>
                <div className="flex flex-col gap-5 mt-2 ">
                    <h4 className="text-2xl font-semibold text-center">
                        {getUser().firstName} {getUser().lastName ? getUser().lastName : ''}
                    </h4>
                    <h4 className="text-xl font-medium">
                        SAPID: {getUser().sapId ? getUser().sapId : 'Not Found!'}
                    </h4>
                    <h4 className="text-xl font-medium">
                        Course: {getUser().courseName ? getUser().courseName : 'Not Found!'}
                    </h4>
                    <h4 className="text-xl font-medium">
                        Semester: {getUser().semester ? getUser().semester : 'Not Found!'}
                    </h4>
                    <h4 className="text-xl font-medium">
                        Boarding Point: {getUser().boardingPoint ? getUser().boardingPoint : 'Not Found!'}
                    </h4>
                    <button onClick={() => change ? setChange(false) : setChange(true)} className="btn w-[250px] h-[30px] bg-black text-white rounded">
                        Change Your Password
                    </button>
                </div>
                { !change ? '':
                    <div className="flex flex-col mt-2 justify-center">
                        <form onSubmit={submitForm}>
                            <div className="p-1 flex flex-col">
                                <label className="text-md font-semibold" htmlFor="oldpassword">Old Password:</label>
                                <div className="flex flex-row justify-start">
                                    <LockClosedIcon 
                                        width={20} 
                                        height={20} 
                                        color="gray"
                                        className="mt-4 -mr-8 z-10 "
                                    />
                                    <input 
                                        className="h-[50px] w-[350px] border-gray-500 rounded shadow pl-10"
                                        type="password" 
                                        name="oldpassword" 
                                        id="oldpassword" 
                                        onChange={(e) => setData({...data, oldPassword: e.target.value })}
                                        placeholder="Enter your old password"
                                    />
                                </div>
                            </div>
                            <div className="p-1 flex flex-col">
                                <label className="text-md font-semibold" htmlFor="newpassword">New Password:</label>
                                <div className="flex flex-row justify-start">
                                    <LockClosedIcon 
                                        width={20} 
                                        height={20} 
                                        color="gray"
                                        className="mt-4 -mr-8 z-10 "
                                    />
                                    <input 
                                        className="h-[50px] w-[350px] border-gray-500 rounded shadow pl-10"
                                        type="password" 
                                        name="newpassword" 
                                        id="newpassword" 
                                        onChange={(e) => setData({...data, newPassword: e.target.value })}
                                        placeholder="Enter your new password"
                                    />
                                </div>
                            </div>
                            <div className="p-1 pt-2 flex flex-col">
                                <button disabled={ loading ? true : false } type="submit" className="bg-black w-[120px] h-[40px] flex flex-row justify-center p-1 rounded-lg text-white text-lg font-semibold">
                                    Change Password
                                    { !loading ? '':
                                        <ArrowPathIcon
                                            className="ml-2 mt-1 animate-spin" 
                                            width={25} 
                                            height={25} 
                                        />
                                    }
                                </button>
                            </div>
                        </form>
                    </div>
                }
            </div>
        </BaseTwo>
    )
}

export default Profile